@font-face {
  font-family: "social";
  src: url("../assets/fonts/myicons.eot");
  src: url("../assets/fonts/myicons.?#iefix") format("embedded-opentype"), url("../assets/fonts/myicons.woff") format("woff"), url("../assets/fonts/myicons.ttf") format("truetype"), url("../assets/fonts/myicons.#myicons") format("svg");
  font-weight: normal;
  font-style: normal; }

.icon {
  font-family: "social"; }
  .icon:before {
    /* Some common icon stuff */ }
  .icon.icon--menu-button:before {
    content: "Ó®Å"; }

* {
  box-sizing: border-box; }

body {
  font-family: 'Rubik', sans-serif; }

.container {
  max-width: 1200px;
  margin: auto; }

li {
  list-style: none; }

a {
  text-decoration: none; }

ul {
  padding: 0;
  margin: 0; }

h2 {
  font-size: 36px;
  margin: 0;
  position: relative;
  z-index: 2;
  color: #00334e; }

.video-wrapper:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.26);
  z-index: 1; }

video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%; }

header {
  position: relative;
  z-index: 1;
  text-align: center;
  padding-top: 200px;
  color: #fff;
  height: 100vh;
  user-select: none; }
  header h1 {
    text-shadow: 0 0 20px #000;
    font-size: 40px;
    font-weight: 900;
    text-transform: uppercase;
    line-height: 1; }
  header p {
    margin-top: 100px;
    font-size: 22px;
    background-color: rgba(0, 51, 78, 0.6);
    padding: 15px;
    border-radius: 6px; }

main {
  position: relative;
  z-index: 2;
  background-color: #fff;
  padding: 60px 0; }
  main h2:before {
    content: "";
    position: absolute;
    left: -16px;
    top: -10px;
    background-image: url(../img/flower.svg);
    background-size: cover;
    width: 40px;
    height: 40px;
    z-index: -1;
    animation: 2s rotation infinite; }

@keyframes rotation {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(360deg); } }
  main ul {
    margin-top: 40px; }
    main ul li img {
      width: 350px;
      height: 250px;
      max-width: 100%; }

section {
  padding: 60px 0;
  background-color: #d0d2d3;
  position: relative;
  z-index: 1; }
  section h2 {
    pointer-events: none; }
  section .contacts {
    margin-top: 40px; }
    section .contacts__item {
      margin-bottom: 20px; }
      section .contacts__item .type {
        margin-bottom: 10px;
        text-shadow: 0 0 20px #fff;
        font-size: 20px; }
      section .contacts__item .contact {
        color: #0099e5;
        font-size: 20px; }
      section .contacts__item a {
        color: #f85a40;
        position: relative;
        padding-left: 40px;
        font-size: 18px; }
        section .contacts__item a:before {
          content: "";
          position: absolute;
          left: 0;
          width: 26px;
          height: 26px;
          background-size: cover;
          top: 50%;
          transform: translateY(-50%);
          transition: .3s; }
        section .contacts__item a.phone:before {
          background-image: url(../img/smartphone.svg); }
        section .contacts__item a.phone:hover:before {
          animation: 1.2s ring 3; }

@keyframes ring {
  15% {
    transform: translateY(-50%) rotate(15deg); }
  25% {
    transform: translateY(-50%) rotate(-35deg); }
  50% {
    transform: translateY(-50%) rotate(15deg); } }
        section .contacts__item a.mail:before {
          background-image: url(../img/paper-plane.svg); }
        section .contacts__item a.mail:hover:before {
          animation: 1s fly; }

@keyframes fly {
  20% {
    transform: translate(-50%, 20%); }
  50% {
    transform: translate(150%, -120%) rotate(15deg); } }

#map {
  background-color: lightcoral;
  padding: 100px 0; }

footer {
  position: relative;
  z-index: 1;
  background-color: #fff;
  text-align: center;
  padding: 10px 0; }

#particles-js {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2; }
