*{
    box-sizing: border-box;
}
body{
    font-family: 'Rubik', sans-serif;
}
.container{
    max-width: 1200px;
    margin: auto;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
}
ul{
	padding: 0;
	margin: 0;
}
h2{
	font-size: 36px;
	margin: 0;
	position: relative;
	z-index: 2;
	color: #00334e;
}